body,html{
  padding: 0;
  margin: 0;
}

li{
  padding: 0;
}

body{
  background-color: #000;
  color: #fff;
  font-family: 'Catamaran', sans-serif;
  font-weight: 900;

}

.header{
  padding-top: 2em;
  position: absolute;
  display: flex;
  align-items: center;
  overflow: hidden;
  white-space: nowrap;
  top: 0;
  width: 93%;
}

.head img{
  border-radius: 10px;
}

.header >*{
  box-sizing: border-box;
}

.header span{
  position: relative;
  font-size: 24px;
  text-transform: uppercase;
  margin-left: 5px;
  -webkit-animation: glow 2s ease-in-out infinite alternate;
  -moz-animation: glow 2s ease-in-out infinite alternate;
  animation: glow 1.5s ease-in-out infinite alternate, leftright 70s infinite;
  animation-timing-function: linear;
}

@keyframes glow {
  from {
      color: #fff;
    text-shadow: 0 0 0.3px #00fff2, 0 0 0.5px #00fff2, 0 0 0.7px #00fff2, 0 0 0.9px #00fff2, 0 0 1.1px #00fff2, 0 0 1.3px #00fff2, 0 0 1.5px #00fff2, 0 0 1.9px #00fff2;
  }
  
  to {
     color: #e9e9e9;
    text-shadow: 0 0 0.5px #00fff2, 0 0 0.7px #00fff2, 0 0 0.9px #00fff2, 0 0 1.1px #00fff2, 0 0 1.3px #00fff2, 0 0 1.5px #00fff2, 0 0 1.7px #00fff2, 0 1 1.9px #00fff2;
  }
}

@keyframes leftright{
  0%{
    left: -4%;
  }

  50%{
    left: 39.5%;
  }
  100%{
    left: -4%;
  }
}

.headerimage-wrap
{
  z-index: 2000;
  width: 150px;
}

.header-image{
  height: 60px;
  width: 60px;
  margin-right: 50px;
  border-radius: 10px;
}

img{
  display: block;
  width: 100%;
  height: 100%;
}

.title{
  text-align: center;
  line-height: 1.1;
  padding: 10px 0;
  font-size: 60px;
  text-shadow: 3px 5px 2px black;
}

h1{
  font-size: 24px;
  font-weight: 900;
  text-transform: uppercase;
  margin: 0;
  text-shadow: 3px 5px 2px black;
}

.head{
  padding: 1em 0;
  display: flex;
  gap: 20px;
}

.head img{
  height: 35px;
  width: 35px;
}

.container{
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.container .flexbox-left,.flexbox-right{
  height: 710px;
  background-color: #2c2c2c;
  display: flex;
  flex-direction: column;
}

.flexbox-left{
  width: 36%;
  align-items: center;
  text-align: center;
}

.flexbox-right{
  width: 54%;
  padding: 0 1.5em;
}

.roadmap-info{
  display: flex;
  width: 95%;
}

.index{
  display: flex;
  font-size: 96px;
  align-items: center;
  text-shadow: 3px 5px 2px #000;
}

.info{
  font-size: 20px;
  line-height: 1.4;
}

.roadmap-images{
  width: 95%;
}

.image-collection{
  display: flex;
  gap: 20px;
  justify-content: space-between;
}

.image-collection img{
  border-radius: 10px;
  width: 200px;
}

.gif-image{
  height: 400px;
  width: 400px;
  border-radius: 20px;
  margin-bottom: 0px;
}

.connect-button .wallet-button {
  text-transform: uppercase;
  padding: 1em 2em;
  height: 45px;
  font-weight: bold;
  font-size: 16px;  
  margin: 0;
  width: 150px;
  background: linear-gradient(180deg, #604ae5 0%, #813eee 100%);
  transition: 0.3s ease;
}

.connect-button .wallet-button:hover {
  animation: pulse 1s infinite;
  border-radius: 1px solid white;
  background: linear-gradient(180deg, #4634ac 0%, #7a2ff3 100%);
  box-shadow: 0 0px 5px 2px #fff;
}

.wallet-button *{
  font-family: 'Catamaran', sans-serif;
  color: #fff;
}


@media (max-width:1280px){
  .container{
    flex-direction: column;
  }  
  .flexbox-left{
    width: 60%;
    margin-top: 900px;
    padding: 2em 1em;
    align-items: center;
  }
  
  .flexbox-right{
    width: 60%;
    padding: 2em 1em;
  }
}